import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { Wrapper, IntroWrapper, DetailsContainer } from './styles'
import { Container } from '../Container'
import { Button } from '../Button'
import { authorProps } from '../../types'

const IntroContext = () => {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            author {
              name
              summary
            }
          }
        }  
      }
    `
  )
  const hero : authorProps = data.site.siteMetadata.author

  return (
    <Wrapper>
      <IntroWrapper>
      <DetailsContainer>
        <Container>
        <h1 className="Hero__summary" dangerouslySetInnerHTML={{ __html:hero.summary}}/>
        <Button>
          Resume
        </Button>
        <h1>ðŸš§ æ–½å·¥ä¸­...</h1>
        </Container>
      </DetailsContainer>
      </IntroWrapper>
    </Wrapper>
  )
}

export default IntroContext